<template>
  <div id="app">
    <!-- h1{강남맛집블로그} -->
    <h1>강남맛집블로그</h1>
<!-- 블로그 추가 -->
<div class="add-blog">
        <input type="text" v-model="newBlog.title" placeholder="블로그 제목을 입력하세요" />
      <!-- 내용입력 -->
      <textarea v-model="newBlog.content" placeholder="블로그 내용을 입력하세요"  ></textarea>
      <!-- 작성자이름 -->
      <input type="text" v-model="newBlog.author" placeholder="작성자 이름을 입력하세요" />
      <!-- 블로그 추가버튼 -->
      <button @click="addBlog">블로그 추가</button>

</div>
<!-- 블로그 리스트 -->
<!-- v-for : Vue.js에서 사용하는 디렉티브 : 
배열을 반복적으로 렌더링해줌 
:key 속성을 사용하여 각 항목을 고유하게 식별하도록 해줌
성능최적화, 렌더링의 효율적인 처리에 중요한 역할
-->
<div class="blog" v-for="(blog, index) in blogs" :key="index">
  <h2>{{ blog.title }}</h2>
  <p>{{ blog.content }}</p>
  <p class="author">{{ blog.author }}</p>
  <!-- 삭제버튼 -->
  <button @click="deleteBlog">삭제</button>
</div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        // 새 블로그 입력 데이타 초기화
        newBlog : {
          title:"",
          content:"",
          author:"",
        }, 
        // 블로그 글 리스트 초기화
        blogs : [
         { title : "강남 최고의 갈비 맛집",
          content : "강남역 근처에 위치한 ABC갈비는 부드럽고 촉촉한 갈비로 유명합니다. ",
          author : "우종범"},
         { title : "수원 아구찜 맛집",
          content : "팔달구에 위치한 777아구찜은 정갈한 맛집입니다. ",
          author : "침착맨"},
        ],
      };
    },
// 새 블로그 추가 메서드
methods : {
  addBlog() {
    // 제목, 내용, 작성자 모두 입력했는지 확인
    if (this.newBlog.title && this.newBlog.content && this.newBlog.author) {
      // 입력받은 값을 블로그에 추가
       // ... 표시 (스프레드 연산자) - 복사를 의미 
      //  값이 존재하면, 블로그에 글 추가하고 입력필드 초기화
      this.blogs.push({ ...this.newBlog});
      // .push 추가
      // 추가 후 입력값 초기화 
      this.newBlog.title = "";
      this.newBlog.content = "";
      this.newBlog.author = "";
    } else {
      // 필드 미입력시 경고메시지 
      alert("모든 필드를 입력하세요.");
    }
  }, 
  deleteBlog(index) {
    this.blogs.splice(index, 1);
    // 1은 하나이 요소만 삭제
    // .splice는, 배열에서 해당 인덱스를 삭제하고 배열을 수정
  },
},
};
</script>

